import React, { useState, useEffect } from "react";
import Header from "../../components/Header";
import Footer from "../../components/Footer"
import { getSubjects } from "../../api/Courses";
import { AiFillTag } from 'react-icons/ai'
import Breadcrumb from "../../components/Breadcrumb";
import useSWR from "swr";
import { useParams } from "react-router-dom";
import { VscUnlock } from 'react-icons/vsc'
import { useNavigate } from "react-router-dom";
import Skeleton from 'react-loading-skeleton'
import 'react-loading-skeleton/dist/skeleton.css'
import Helmet from "react-helmet";

function Subjects() {
    const navigate = useNavigate()
    const params = useParams()
    const id1 = params.id
    const [subjects, setSubjects] = useState([])
    const { data, error } = useSWR(process.env.REACT_APP_BACKEND + `/subjects/${id1}/list`, async url => await getSubjects(url), { revalidateOnFocus: false, revalidateOnReconnect: true })

    useEffect(() => {
        if (typeof data !== typeof undefined) {
            if (data?.success) {
                setSubjects(data?.data?.subjects)
            }
        }
    }, [data, error])

    const handleClick = (id) => {
        navigate(`/programmes/${id1}/${id}`)
    }

    return (
        <div className={`w-full h-full bg-back pt-20 dark:bg-background`}>
            <Helmet>
                <title>{`${data?.data?.course?.title ? data?.data?.course?.title + ' | ' : ""}Aakhyaan Academy`}</title>
            </Helmet>
            <Header page="programmes" />
            <Breadcrumb props={[{ header: "Programmes", link: "programmes" }, { header: data?.data?.course?.title, link: `programmes/${data?.data?.course?._id}` }]} />
            <div className="dark:bg-background bg-back flex w-full min-h-full pb-20 px-10 justify-center">
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-24 md:gap-10 xl:gap-16 items-center justify-center py-20">
                    {!data && !error ?
                        <>
                            {[...Array(2)].map((val, i) =>
                                <div key={i} >
                                    <div ><Skeleton width={240} height={180} baseColor='gray' /></div>
                                </div>
                            )}
                        </>
                        :
                        <>
                            {subjects ? (
                                subjects.map((subject, i) =>
                                    <div
                                        key={i}
                                        onClick={() => handleClick(subject._id)}
                                        className={`max-w-sm lg:max-w-full flex cursor-pointer hover:translate-y-1 justify-center`}>
                                        {subject?.thumbnail ?
                                            <img
                                                className="ring-1 ring-opacity-5 ring-blue-900 h-[160px] w-[160px] md:h-[200px] md:w-[200px] object-fit rounded-l-xl"
                                                alt=""
                                                src={subject?.thumbnail}
                                            />
                                            :
                                            <></>
                                        }
                                        <div className="relative shadow-xl h-[160px] w-[160px] md:h-[200px] md:w-[200px] border-transparent border-gray-400 dark:bg-gray-800 bg-back dark:text-mainText/80 rounded-md p-8 flex flex-col justify-between">
                                            <div className="my-auto flex flex-col items-center justify-center">
                                                <p className="absolute bottom-2 right-2 flex gap-2 text-sm items-center">
                                                    <VscUnlock />
                                                    <span>Free</span>
                                                </p>
                                                <div className="font-bold text-2xl">{subject?.title}</div>
                                            </div>
                                        </div>
                                    </div>
                                )
                            ) : (
                                <div className="text-mainText text-xl">
                                    There are no subjects available at the moment.
                                </div>
                            )}
                        </>
                    }
                    {error ?
                        <div className="text-warningText text-xl w-full h-full flex items-center justify-center">{error?.response?.data?.message}</div>
                        :
                        <></>
                    }
                </div>
            </div>
            <Footer />
        </div>
    );
}


export default Subjects;



